import Vue from 'vue';
import wx from 'weixin-js-sdk';

export default {
    init: jsApiList =>
        new Promise(resolve =>
            Vue.http
                .get('mp/getToken', {
                    params: {
                        href: location.href.split('#')[0]
                    }
                })
                .then(result => {
                    wx.config({
                        // debug: true, // ,apialertpclogpc
                        appId: result.data.appid, // 
                        timestamp: result.data.timestamp, // 
                        nonceStr: result.data.nonceStr, // 
                        signature: result.data.signature, // 
                        jsApiList: ['checkJsApi'].concat(jsApiList || []) // JS
                    });
                    wx.ready(() => {
                        // configreadyconfigconfigreadyready
                        wx.checkJsApi({
                            jsApiList: jsApiList || [], // JSJS2,
                            success: res => {
                                // apitruefalse
                                // {"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
                                resolve(wx);
                            }
                        });
                    });
                    wx.error(res => {
                        // configerrorconfigdebugresSPA
                        console.warn('JS SDK', res);
                    });
                })
        )
};



